import React from 'react';
import faqData from '@/data/faqs.json';
import ServiceSummaryAccordion from '@/components/about/ServiceSummaryAccordion';

export default function FAQPage() {
  // Flatten categories for Google's FAQ Schema
  const allFaqs = [
    ...faqData.roadsideAssistance,
    ...faqData.accidentCollision,
    ...faqData.specialtyTowing
  ];

  const jsonLd = {
    "@context": "https://schema.org",
    "@type": "FAQPage",
    "mainEntity": allFaqs.map((faq) => ({
      "@type": "Question",
      "name": faq.question,
      "acceptedAnswer": {
        "@type": "Answer",
        "text": faq.answer
      }
    }))
  };

  const categories = [
    { key: 'roadsideAssistance', label: 'Roadside Assistance' },
    { key: 'accidentCollision', label: 'Accident & Collision' },
    { key: 'specialtyTowing', label: 'Specialty Towing' }
  ];

  return (
    <main className="bg-white min-h-screen">
      <script
        type="application/ld+json"
        dangerouslySetInnerHTML={{ __html: JSON.stringify(jsonLd) }}
      />

      {/* Hero Header */}
      <section className="bg-slate-950 py-16 text-center border-b border-white/5">
        <div className="container mx-auto px-6">
          <h1 className="text-4xl md:text-6xl font-black italic uppercase text-white mb-3 tracking-tighter">
            FAQ <span className="text-red-600">Hub</span>
          </h1>
          <p className="text-slate-500 font-bold uppercase tracking-[0.3em] text-[10px]">
            National Capital Region Support
          </p>
        </div>
      </section>

      {/* Categorized Content */}
      <section className="py-12 md:py-20">
        <div className="container mx-auto px-6 max-w-4xl">
          {categories.map((cat, idx) => (
            <div key={cat.key} className={idx !== 0 ? "mt-20" : ""}>
              {/* Category Title */}
              <div className="flex items-center gap-4 mb-10">
                <div className="h-px flex-1 bg-slate-100" />
                <h2 className="text-2xl font-black italic uppercase text-slate-900 tracking-tight shrink-0 px-4">
                  {cat.label}
                </h2>
                <div className="h-px flex-1 bg-slate-100" />
              </div>

              {/* Accordion List */}
              <div className="bg-slate-50/50 rounded-[2rem] p-2 md:p-6 border border-slate-100">
                {faqData[cat.key].map((faq) => (
                  <ServiceSummaryAccordion 
                    key={faq.id}
                    title={faq.question}
                    content={faq.answer}
                  />
                ))}
              </div>
            </div>
          ))}

          {/* Emergency Support CTA */}
          <div className="mt-20 bg-slate-900 rounded-[2.5rem] p-10 text-center text-white relative overflow-hidden">
            <div className="absolute top-0 right-0 w-64 h-64 bg-red-600/10 blur-[100px] rounded-full" />
            <h3 className="text-2xl font-black italic uppercase mb-4 relative z-10">Still Stuck?</h3>
            <p className="mb-8 opacity-70 font-medium relative z-10">Our dispatchers are standing by 24/7 to provide live support.</p>
            <a href="tel:548-388-4586" className="relative z-10 inline-block bg-red-600 text-white px-10 py-4 rounded-xl font-black italic uppercase text-lg hover:bg-red-700 transition-colors shadow-xl shadow-red-600/20">
              Call Now: 548-388-4586
            </a>
          </div>
        </div>
      </section>
    </main>
  );
}